export default {
	data() {
		return {
			bannerList: []
		};
	},
	props: {
		bannerHeight: {
			default: "300"
		},
		bannerType: {
			default: '0'
		},
	},
	created() {
		this.$app.request(this.$app.API.BANNER_LIST, {}, res => {
			const bannerList = []
			for (let v of res.data) {
				bannerList.push({
					img: v.img_url,
					url: v.gopage,
				})
			}
			
			this.bannerList = bannerList
		})
	},

	computed: {
		bannerHeightComputed() {
			return uni.upx2px(this.bannerHeight) + 'px';
		}
	},
	methods: {
		goPage(url) {
			this.$app.goPage(url)
		}
	}
}
